import React from 'react';
import { shape } from 'prop-types';

const colors = ['#ed6f4d', '#facaa5', '#71c3ad', '#4ca9e0', '#54899c', '#62b3e3', '#c6dcda'];
const shapeNames = ['line', 'square', 'circle'];

const makeRandomInt = (min: number, max: number) => {
  return Math.floor(Math.random() * (max - min + 1)) + min;
};

let shapes = [];

const makeShapes = () => {
  shapes = [];
  for (let i = 0; i < 40; i++) {
    const shape = shapeNames[makeRandomInt(0, shapeNames.length - 1)];
    shapes.push({
      x: makeRandomInt(0, 100),
      y: makeRandomInt(0, 100),
      width: makeRandomInt(shape === 'line' ? 10 : 5, shape === 'line' ? 15 : 10),
      rotate: makeRandomInt(0, 180),
      color: colors[makeRandomInt(0, colors.length - 1)],
      shape: shapeNames[i % 3]
    });
  }
};

makeShapes();

const shapeStyles = ({ x, y, width, shape, rotate, color }) => ({
  position: 'absolute' as const,
  top: `${y}%`,
  left: `${x}%`,
  width: `${width}px`,
  height: `${shape === 'line' ? '2' : width}px`,
  opacity: 1 - y / 100,
  transform: `rotate(${rotate}deg)`,
  backgroundColor: shape === 'line' ? color : 'transparent',
  borderColor: color,
  border: shape === 'line' ? '' : `2px solid ${color}`,
  borderRadius: shape === 'circle' ? '50%' : 0,
  zIndex: -1,
  transition: 'all 0.5s ease-in-out'
});

const Shapes = () => {
  const [curShapes, setShapes] = React.useState(shapes);
  return (
    <>
      <button
        onClick={() => {
          setShapes(
            curShapes.map(shape => ({
              ...shape,
              x: makeRandomInt(0, 100),
              y: makeRandomInt(0, 100)
            }))
          );
        }}
      >
        random
      </button>
      {curShapes.map((shape, i) => (
        <React.Fragment key={`${shape.width}${shape.rotate}${shape.shape}${shape.color}`}>
          <div style={shapeStyles(shape)} />
          <div style={shapeStyles(shape)} />
        </React.Fragment>
      ))}
    </>
  );
};

export default Shapes;
